    const PushSetTools = async () => {
        const Tools = []
        leftChecked.map((value) => {
            return Tools.push(value.id)
        })

        console.log(Tools);

        const options = {
            method: 'POST',
            headers: {
                'Authorization': "Bearer " + localStorage.getItem('token'),
                "Content-type": "application/json",
                "Accept": "application/json",
            },
            body: JSON.stringify({
                account: pushTrx.account,
                actions: [
                    {
                        account: "m.federation",
                        name: "setbag",
                        authorization: [
                            {
                                actor: pushTrx.account,
                                permission: "active",
                            },
                        ],
                        data: {
                            account: pushTrx.account,
                            items: Tools.slice(0, 3),
                        },
                    },
                ]

            })
        };


        const SendPushTransaction = new Promise(async (resolve, rejected) => {
            await fetch("/push_transaction", options)
                .then(res => res.json())
                .then(json => {
                    console.log(json);
                    if (json.transaction_id) {
                        resolve(json)
                    } else {
                        // setLogin(false)

                        rejected(json)
                    }
                })
                .catch(err => rejected(err));
        });

        toast.promise(
            SendPushTransaction,
            {
                pending: 'Promise is pending',
                success: {
                    render({ data }) {
                        return `Succeed : ${data.transaction_id.slice(0, 10)}`
                    },
                },
                error: {
                    render({ data }) {
                        if (data.GetSingError === 401) {
                            return `Failed : Token expired please new login`
                        } else {
                            // When the promise reject, data will contains the error
                            return `Failed : ${data.DetailsError[0].what}`
                        }
                    }
                }
            }, { theme: window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light" }
        )
    }

